// material-ui
import {
  Typography, Grid, Button, Avatar,
  Card,
  Divider,
  Tooltip,
  Skeleton,
} from '@mui/material';

import { useEffect, useState } from 'react';
// project import
import {
  ArrowLeftOutlined,
  PlayCircleOutlined,
  PauseCircleOutlined

} from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import { BASE_URL, post } from 'Urls/api';
import { useParams } from 'react-router-dom';
import SyncLoader from 'react-spinners/SyncLoader';

const CourseDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [product_name, setproduct_name] = useState('')
  const [price, setprice] = useState('')
  const [image, setImage] = useState('')
  const [videodata, setVideoData] = useState([])
  const [firstVideo, setFirstVideo] = useState('')
  const [firstVideotitle, setFirstVideotitle] = useState('')
  const [firstVideoUrl, setFirstVideoUrl] = useState('')
  const [firstVideoThumbnail, setFirstVideoThumbnail] = useState('')
  const [videoLoading, setVideoLoading] = useState(true)

  const getProductById = async () => {
    try {
      const postData = {
        package_id: id
      }
      const apiData = await post('package/getByProgramId', postData); // Specify the endpoint you want to call
      console.log("Course Details")

      console.log(apiData)

      if (apiData.error) {
        console.log("ERROR")
        setVideoLoading(false)

      } else {

        setImage(apiData?.data?.course?.image)
        setproduct_name(apiData?.data?.course?.package_name)
        setprice(apiData?.data?.course?.description)
        setVideoData(apiData?.data?.videos)
        const data = apiData.data.videos[0].video_url
        let newHeight;

        if (window.innerWidth <= 600) { // 600px is generally the breakpoint for mobile devices
          newHeight = '100%';
        } else {
          newHeight = '520px';
        } // Set your desired height here
        const updatedVideoUrl = data.replace('height="100%"', `height="${newHeight}"`);
        setFirstVideoUrl(updatedVideoUrl)
        setFirstVideo(apiData?.data?.videos[0]?.description)
        setFirstVideoThumbnail(apiData?.data?.videos[0]?.thumbnail)
        setFirstVideotitle(apiData?.data?.videos[0]?.title)
        setCurrentVideo(apiData?.data?.videos[0]);
        setVideoLoading(false)
      }
    } catch (error) {
      setVideoLoading(false)


    }
  }
  const [currentVideo, setCurrentVideo] = useState({})
  useEffect
    (() => {
      getProductById()
    }, [])

  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={12} md={6}>
          <Grid container spacing={2}>
            <Grid item xs={2} md={2} align="right">
              <Tooltip title="Back">


                <Button
                  onClick={() => {
                    navigate(`/courses`)
                  }
                  }
                  className='poppinsRegularBold'
                  sx={{
                    // width: '100px',
                    paddingInline: '20px',
                    display: "block",
                    color: "#03356b",
                    backgroundColor: "none",
                    fontSize: '16px',
                    fontWeight: 600,
                    borderRadius: '50px',
                    textTransform: 'capitalize',
                    transition: 'background-color 0.3s, color 0.3s', // Add transition properties
                    transitionDelay: '0.3s', // Add transition delay for hover
                    // border: '1px solid #03356b',
                    '&:hover': {
                      backgroundColor: "#03356b",
                      color: 'white', // Change text color to blue on hover
                    },
                  }} >
                  <ArrowLeftOutlined />
                </Button>
              </Tooltip>

            </Grid>
            <Grid item xs={3} md={1} align="right">
              <Avatar
                variant="square"
                src={`${BASE_URL}${image}`}
                style={{
                  backgroundColor: "#03356b"
                }} />

            </Grid>
            <Grid item xs={7} md={9} align="left">
              <Typography variant="h5" style={{ fontWeight: 200 }}>{product_name}</Typography>
              <Typography variant="h6" style={{ fontWeight: 200, color: 'gray' }}>{price}</Typography>
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12} md={12}>
          <Divider />
        </Grid>
      </Grid>


      <Grid container spacing={2} mt={2}>

        <Grid item xs={12} md={12} style={{ borderRadius: '20px' }}>
          <Grid container spacing={2}>
            <Grid item xs={12} md={12} align="center">
              {videoLoading ?
                <SyncLoader color="#03356b" loading={videoLoading} size={15} /> :
                <></>
              }
            </Grid>
            <Grid item xs={12} md={8} style={{ borderRadius: '10px' }}>

              {firstVideoUrl === null || firstVideoUrl === undefined || firstVideoUrl === "" ? <>
                <Skeleton variant="rectangular" height={500} width="100%" />
              </> : <div
                dangerouslySetInnerHTML={{
                  __html: ` ${firstVideoUrl}`
                }}
              />}
              <Typography variant='h4'

                style={{ color: '#041d34' }} >
                {firstVideotitle === null ||
                  firstVideotitle === undefined ||
                  firstVideotitle === "" ? <>
                  <Skeleton variant="rectangular" height={50} width={100} />
                </> : firstVideotitle}
              </Typography>
            </Grid>
            <Grid item xs={12} md={4} style={{ borderRadius: '20px' }}>
              {/* <SubscribedUsers product_id={id} /> */}

              <Card style={{ height: 'auto', padding: '2px', boxShadow: 'none', borderRadius: '10px' }}>
                <Typography variant='h5'

                  style={{ color: '#041d34', marginBlock: '2%' }} >
                  Course Outline :
                </Typography>
                <Grid container spacing={2} mt={2}>
                 
                  <Grid item xs={12} md={12} style={{height:'500px',overflowY:'scroll'}}>
                    <Grid container spacing={1}>
                      {videodata.length === 0 ? <></> :
                        <> {videodata.map((itemD, index) => {
                          return (
                            <>
                              <Grid item xs={12} md={3}
                                onClick={() => {
                                  setVideoLoading(true)
                                  console.log(itemD)
                                  const data = itemD.video_url
                                  let newHeight;

                                  if (window.innerWidth <= 600) { // 600px is generally the breakpoint for mobile devices
                                    newHeight = '100%';
                                  } else {
                                    newHeight = '520px';
                                  } // Set your desired height here
                                  const updatedVideoUrl = data.replace('height="100%"', `height="${newHeight}"`);
                                  setFirstVideoUrl(updatedVideoUrl)
                                  setFirstVideo(itemD?.description)
                                  setFirstVideoThumbnail(itemD?.thumbnail)
                                  setFirstVideotitle(itemD?.title)
                                  setCurrentVideo(itemD);
                                  // scroll to top 
                                  window.scrollTo({
                                    top: 0,
                                    behavior: "smooth"
                                  });
                                  setTimeout(() => {
                                    setVideoLoading(false)
                                  }, [1000])

                                }}
                                style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer' }}>
                                <div style={{ position: 'relative', opacity: 0.5 }}>
                                  <Avatar
                                    variant='square'
                                    src={`${BASE_URL}${itemD.thumbnail}`} id="ImageHide" alt="Circle" style={{
                                      height: '50px',
                                      width: '100px',
                                      boxShadow: ' 0px 2px 30px -15px rgba(94,94,107,0.67)',
                                      // borderRadius: '10px'
                                    }} />
                                  {currentVideo === itemD ?
                                    <PauseCircleOutlined
                                      style={{
                                        position: 'absolute',
                                        top: '50%', left: '50%',
                                        transform: 'translate(-50%, -50%)', fontSize: '30px'
                                      }} /> :
                                    <PlayCircleOutlined style={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', fontSize: '30px' }} />}

                                </div>

                              </Grid>
                              <Grid item xs={12} md={9} align="left">
                                <Typography variant='h5'


                                  style={{ color: '#041d34' }} >
                                  {itemD.title}
                                </Typography>
                                <Typography variant='h6'


                                  style={{ color: 'gray' }} >
                                  {itemD.description}
                                </Typography>
                              </Grid>
                              <Divider width="100%" />
                            </>
                          )
                        })}</>}

                    </Grid>
                  </Grid>
                </Grid>
              </Card>
            </Grid>
          </Grid>
          {/* video  */}
        </Grid>

      </Grid>

    </>
  )
};

export default CourseDetail;
